<template>
  <div>
    <div v-for="(audio, index) in paginatedAudio" :key="index" class="audio-container">
      <audio :src="audio.src" controls></audio>
      <el-rate v-model="audio.rating" show-text :max="5" :texts="['非常差','差','一般','好','非常好']" ></el-rate>
      <el-select v-model="audio.choice" placeholder="请选择">
        <el-option label="fear" value="fear"></el-option>
        <el-option label="anger" value="anger"></el-option>
        <el-option label="disgust" value="disgust"></el-option>
        <el-option label="happiness" value="happiness"></el-option>
        <!-- <el-option label="选项5" value="option5"></el-option> -->
      </el-select>
    </div>
    <el-pagination
      @current-change="handlePageChange"
      :current-page="currentPage"
      :page-size="itemsPerPage"
      layout="prev, pager, next"
      :total="audioFiles.length">
    </el-pagination>
    <el-button type="primary" @click="submitRatings" :disabled="!allRated">提交评分</el-button>
  </div>
</template>

<script>
import axios from "axios"
export default {
  data() {
    return {
      audioFiles: Array.from({ length: 10 }, (_, i) => ({
        src: `/audio/${i + 1}.wav`,
        rating: 0,
        choice: ''
      })),
      currentPage: 1,
      itemsPerPage: 5,
      ratings: []
    };
  },
  computed: {
    paginatedAudio() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.audioFiles.slice(start, end);
    },
    allRated() {
      return this.audioFiles.every(audio => audio.rating > 0 && audio.choice !== '');
    }
  },
  methods: {
    handlePageChange(page) {
      this.saveCurrentPageRatings();
      this.currentPage = page;
    },
    saveCurrentPageRatings() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      this.paginatedAudio.forEach((audio, index) => {
        this.ratings[start + index] = {
          src: audio.src,
          rating: audio.rating,
          choice: audio.choice
        };
      });
    },
    submitRatings() {
      this.saveCurrentPageRatings();
      axios.post('/api/rating', this.ratings)
      .then(response => {
        // 处理响应
        console.log('评分上传成功', response.data);
      })
      .catch(error => {
        // 处理错误
        console.error('评分上传失败', error);
      });
    }
  },
  mounted() {
    this.ratings = Array.from({ length: this.audioFiles.length }, () => ({
      src: '',
      rating: 0,
      choice: ''
    }));
  }
};
</script>

<style scoped>
.audio-container {
  margin-bottom: 40px; /* 增加间隔 */
}
</style>
